.add-projects{
    border-top: 0;
    border-left:0;
    border-right: 0;
    border-bottom:2px black solid;
    outline: none;
    margin-top: 55px;
    margin-bottom: 10px;
}

.allproject{
    margin-top: 20px;}

.project_list{

    display: flex;
    justify-content: space-between;
    margin: 9px;
    font-weight: bold;
    width: 20rem;
    border-bottom: 4px solid black;
    border-right: 4px solid black;
}


.creating_pointer{
    cursor: pointer;
    

}
.delete , .show , .add-task{
    border: 0;
    background-color:white;
    transition: transform 0.5s ease;

}




.delete:hover , .creating_pointer:hover , .add-task:hover{
    transform: scale(1.08);
}




.show:focus{
    color: rgb(0, 0, 0);
}

h3{
    /* font-size: xx-large; */
    text-align: center;
