
.task_box{

    background-color: rgb(255, 255, 255);
    border: 2px rgb(0, 0, 0) solid;
    margin-top: 20px;
    text-align: center;
    width: 15rem;
    margin-left: 5rem;  

}




.input-field{

    border-top: 0;
    border-left:0;
    border-right: 0;
    border-bottom:2px black solid;
    outline: none;
}



.addbtn , .input-field , .exp{
    margin: 10px;
    background-color: transparent;

}


.text{
   font-weight: bolder;
    font-size: 22px;
    margin-bottom: 22px;
}

.task_box_close_btn{
    cursor: pointer;
    color: rgb(251, 8, 8);
    text-align: right;
   

}

.addbtn{
    transition: background-color 0.7s ease;
}


.addbtn:hover{
    color: aliceblue;
    background-color: black;

}